.code-page-container {
  padding-top: 2em;

  table {
    width: 100%;
    max-width: 1024px;
    margin: 0 auto;

    td,
    th {
      padding: .25em .5em;
    }

    th {
      font-weight: bold;
    }

    td {
      vertical-align: middle;
    }
  }

  .code-page-text-1 {
    opacity: .7;
    font-size: .7em;
  }

  .code-page-project-link {
    display: block;

    &:hover {
      .code-page-project-name {
        border-bottom: 1px solid silver;
      }
    }
  }
}