.home-page-skills-container {
  padding: 2em;

  .home-page-skills-area {
    color: silver;
  }

  .block-heading-content {
    display: flex;
    justify-content: center;

    h1 {
      font-size: 2rem;
      border-bottom: 1px solid #eee;
      width: 50%;
    }
  }

  .block-content-container {
    margin-top: 4em;
  }

  .home-page-skills-list {
    text-align: center;

    svg {
      width: 100px;
      margin: .25em .75em;
    }
  }
}